//api/http.js
import { checkTokenExpiration } from './auth';

// ÁéØÂ¢ÉÈÖçÁΩÆ
export const ENV_CONFIG = { 
  development: 'http://localhost:3000',
  production: 'https://apifoxmock.com/m1/4728220-0-default'
}

// ÂÖ®Â±ÄÈÖçÁΩÆ
const GLOBAL_CONFIG = {
	timeout: 10000, // 10ÁßíË∂ÖÊó∂
	retryCount: 1, // Â§±Ë¥•ÈáçËØïÊ¨°Êï∞
}

/**
 * Â¢ûÂº∫ÂûãËØ∑Ê±ÇÂ∞ÅË£Ö
 * @param {string} url - ËØ∑Ê±ÇÂú∞ÂùÄ
 * @param {object} options - ËØ∑Ê±ÇÈÖçÁΩÆ
 * @returns {Promise} 
 */
// ‰øÆÊîπÈªòËÆ§ÂØºÂá∫‰∏∫ÂëΩÂêçÂØºÂá∫
export const http = {
  get: (url, options = {}) => enhancedHttp(url, { ...options, method: 'GET' }),
  post: (url, data, options = {}) => enhancedHttp(url, { ...options, method: 'POST', data }),
  put: (url, data, options = {}) => enhancedHttp(url, { ...options, method: 'PUT', data }),
  delete: (url, options = {}) => enhancedHttp(url, { ...options, method: 'DELETE' })
}


export async function enhancedHttp(url, options = {}) {
  // ÂêàÂπ∂ÈÖçÁΩÆ
	const mergedOptions = {
		method: 'GET',
		data: {},
		headers: {},
		...options
	}

	// ËØ∑Ê±ÇÊã¶Êà™Âô®
	const { headers, shouldAuth = true } = await requestInterceptor()
  
	// ÊûÑÈÄ†ËØ∑Ê±ÇÂ§¥
	const finalHeaders = {
		'Content-Type': 'application/json',
		'Access-Control-Allow-Origin': '*',
		...headers,
		...mergedOptions.headers
	}

	// TokenÂ§ÑÁêÜ
	if (shouldAuth) {
		let validToken = checkTokenExpiration()
		
		  // Ëá™Âä®Âà∑Êñ∞Êú∫Âà∂
		if (!validToken && shouldAuth) {
		    try {
				const newToken = await checkTokenExpiration()
				validToken = newToken
		    } catch (e) {
				console.error('TokenÂà∑Êñ∞Â§±Ë¥•:', e)
		    }
		}
		if (validToken) {
			finalHeaders.Authorization = `Bearer ${validToken}`
		}
	}

	// ÂèëËµ∑ËØ∑Ê±Ç
	try {
		 // üî¥ Êñ∞Â¢ûËØ∑Ê±ÇÂºÄÂßãÊó•ÂøóÔºàÊ≠•È™§3Ôºâ
		    console.debug('[HTTP] ËØ∑Ê±ÇÂºÄÂßã', {
		      timestamp: new Date().toISOString(),
		      url: ENV_CONFIG[process.env.NODE_ENV] + url,
		      method: mergedOptions.method,
		      data: mergedOptions.data,
		      headers: finalHeaders
		    });
		
		    const response = await requestWithRetry({
		      url: ENV_CONFIG[process.env.NODE_ENV] + url,
		      data: mergedOptions.data,
		      method: mergedOptions.method,
		      header: finalHeaders,
		      timeout: GLOBAL_CONFIG.timeout
		    });
		
		    // üî¥ Êñ∞Â¢ûÂéüÂßãÂìçÂ∫îÊó•ÂøóÔºàÊ≠•È™§3Ôºâ
		    console.debug('[HTTP] ÂéüÂßãÂìçÂ∫î', {
		      status: response.statusCode,
		      headers: response.header,
		      data: response.data
		    });
		
		    // ÂìçÂ∫îÊã¶Êà™Âô®
		    return responseInterceptor(response);
		  } catch (error) {
		    // üî¥ Êñ∞Â¢ûÈîôËØØËØ¶ÊÉÖÊó•ÂøóÔºàÊ≠•È™§3Ôºâ
		    console.error('[HTTP] ËØ∑Ê±ÇÂ§±Ë¥•', {
		      errorType: error.constructor.name,
		      message: error.message,
		      stack: error.stack,
		      retryCount: GLOBAL_CONFIG.retryCount,
		      url: ENV_CONFIG[process.env.NODE_ENV] + url
		    });
		    
		    errorHandler(error);
		    throw error;
		  }
}
const platformAdapter = {
  processResponse(res) {
    // Â§ÑÁêÜÂæÆ‰ø°Â∞èÁ®ãÂ∫èËøîÂõûÂÄºÔºàËøîÂõûÊï∞ÁªÑÔºâ
    if (Array.isArray(res)) {
      const [error, successRes] = res;
      return error ? { error } : { response: successRes };
    }
    // Â§ÑÁêÜH5ËøîÂõûÂÄºÔºàËøîÂõûPromiseÔºâ
    return { response: res };
  }
};
/**
 * Â∏¶ÈáçËØïÊú∫Âà∂ÁöÑËØ∑Ê±Ç
 */
// ‰øÆÊ≠£requestWithRetryÂáΩÊï∞
// http.js ‰øÆÊîπÂêé
async function requestWithRetry(params, retry = GLOBAL_CONFIG.retryCount) {
  for (let i = 0; i <= retry; i++) {
    try {
      // Áªü‰∏Ä‰ΩøÁî® try-catch Â§ÑÁêÜÊâÄÊúâÂπ≥Âè∞
      const response = await uni.request(params);
      
      // ÂæÆ‰ø°Â∞èÁ®ãÂ∫èËøîÂõûÁöÑÊòØÊï∞ÁªÑ [error, res]ÔºåÈúÄË¶ÅÁâπÊÆäÂ§ÑÁêÜ
      if (Array.isArray(response)) {
        const [error, res] = response;
        if (error) throw error;
        return { 
          statusCode: res.statusCode,
          data: res.data,
          headers: res.header 
        };
      }
      
      // H5ÂèäÂÖ∂‰ªñÂπ≥Âè∞Áõ¥Êé•ËøîÂõûÂØπË±°
      return {
        statusCode: response.statusCode || 200,
        data: response.data,
        headers: response.headers || {}
      };
    } catch (error) {
      if (i === retry) throw error;
    }
  }
}

/**
 * ËØ∑Ê±ÇÊã¶Êà™Âô®ÔºàÂèØÊâ©Â±ïÔºâ
 */
async function requestInterceptor() {
  // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†ÂÖ®Â±ÄËØ∑Ê±ÇÂâçÈÄªËæë
	return {
		headers: {},
		shouldAuth: true
	}
}

/**
 * ÂìçÂ∫îÊã¶Êà™Âô®
 */
function responseInterceptor(response) {
  // 1. Âü∫Á°ÄÂØπË±°Ê†°È™å
  if (!response || typeof response !== 'object') {
    throw new Error('ÂìçÂ∫îÂØπË±°Ê†ºÂºèÈîôËØØ: ÈùûÂØπË±°Á±ªÂûã');
  }

  // 2. ÂÖ≥ÈîÆÂ≠óÊÆµÊ†°È™å
  const { statusCode, data } = response;
  if (typeof statusCode === 'undefined') {
    throw new Error('ÂìçÂ∫îÁº∫Â∞ëÁä∂ÊÄÅÁ†Å (statusCode)');
  }
  if (typeof data === 'undefined') {
    throw new Error('ÂìçÂ∫îÁº∫Â∞ëÊï∞ÊçÆÂ≠óÊÆµ (data)');
  }

  // 3. Áä∂ÊÄÅÁ†ÅÂ§ÑÁêÜ
  switch (true) {
    case statusCode === 401:
      uni.navigateTo({ url: '/pages/login' });
      throw new Error('ÁôªÂΩïÁä∂ÊÄÅÂ∑≤ËøáÊúü');
    case statusCode >= 500:
      throw new Error(`ÊúçÂä°Âô®ÈîôËØØ (${statusCode})`);
    case statusCode >= 400:
      throw new Error(`ÂÆ¢Êà∑Á´ØÈîôËØØ (${statusCode})`);
    case statusCode === 200:
      return response.data; // Ê≠£Â∏∏ËøîÂõûÊï∞ÊçÆ
    default:
      throw new Error(`Êú™Áü•Áä∂ÊÄÅÁ†Å: ${statusCode}`);
  }
}

/**
 * Áªü‰∏ÄÈîôËØØÂ§ÑÁêÜ
 */
function errorHandler(error) {
	console.error('ËØ∑Ê±ÇÈîôËØØ:', error)
	uni.showToast({
		title: error.message || 'ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•',
		icon: 'none',
		duration: 2000
	})
}